
.mint-toast {
    position: fixed;
    max-width: 80%;
    border-radius: 5px;
    background: rgba(0, 0, 0, 0.7);
    color: #fff;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    text-align: center;
    z-index: 1000;
    -webkit-transition: opacity .3s linear;
    transition: opacity .3s linear
}
.mint-toast.is-placebottom {
    bottom: 50px;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0)
}
.mint-toast.is-placemiddle {
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%)
}
.mint-toast.is-placetop {
    top: 50px;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0)
}
.mint-toast-icon {
    display: block;
    text-align: center;
    font-size: 56px
}
.mint-toast-text {
    font-size: 14px;
    display: block;
    text-align: center
}
.mint-toast-pop-enter, .mint-toast-pop-leave-active {
    opacity: 0
}

html,
.container,
.imgbox {
  width: 100%;
}
body {
  margin: 0;
}
.imgbgc {
  width: 100%;
  margin-top: -0.01rem;
  position: relative;
}
.imgbgc .absolute {
  position: absolute;
}
.imgbgc img {
  width: 100%;
}
.imgbgc .link {
  color: #fff;
  bottom: 1.4rem;
  left: 0;
  right: 0;
  margin: auto;
  width: 3rem;
  height: 1rem;
  font-size: 0.28rem;
  line-height: 1rem;
  text-align: center;
}
.btn {
  position: fixed;
  bottom: 0.4rem;
  left: 0;
  right: 0;
  width: 5.43rem;
  height: 1.03rem;
  margin: auto;
}
.btn img {
  width: 100%;
  height: 100%;
}
